.csvpreviewer::-webkit-scrollbar {
  -webkit-appearance: none;
  background-color: #ccc;
  width: 2px;
}

.csvpreviewer::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

.csvpreviewer {
  /* find out how to fill the width and scroll the overflow*/
  width: 820px;
  max-width: 100%;
  overflow-x: scroll;
}

.csvpreviewer table th {
  padding: 3px;
}

.csvpreviewer table select {
  max-width: 10em;
}

.csvpreviewer table td {
  border: 1px solid #ccc;
  border-collapse: collapse;
  padding: 5px;
  background: #fff;
}

#HasHeader,
#ClearData {
  padding-left: 5px;
}




